import React from 'react';
import users from '../users.json';

console.log(users);

function User(){
    return(
        <div className='users'>
            {users.map(user => (
                <div className='user'>
                    <span>Name: </span>
                    <label>{ user.name }</label>
                    <br/>
                    <span>Age: </span>
                    <label>{ user.age }</label>
                </div>
            ))}
        </div>
    );
}

export default User;